  100% {
    -webkit-transform: scale(1) translate(0, 0);
    opacity: 1; } }

@-webkit-keyframes gold4 {
  0% {
    -webkit-transform-origin: 50% 50%;
    -webkit-transform: scale(0.1);
    opacity: 0; }
  100% {
    -webkit-transform-origin: 50% 50%;
