.step_init_x {
  -webkit-transform: translateX(-100px);
  transform: translateX(-100px);
  opacity: 0; }

.step_animateX {
  -webkit-animation: step_animateX 1s ease forwards;
  animation: step_animateX 1s ease forwards; }

@keyframes step_animateX {
  0% {
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1; } }

@-webkit-keyframes step_animateX {
  0% {
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1; } }

.step_init_y {
  -webkit-transform: translateX(-100px);
  transform: translateX(-100px);
  opacity: 0; }

.step_animateY {
  -webkit-animation: step_animateY 1s ease forwards;
  animation: step_animateY 1s ease forwards; }

@keyframes step_animateY {
  0% {
    -webkit-transform: translateY(100px);
    transform: translateY(100px);
    opacity: 0; }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1; } }

@-webkit-keyframes step_animateY {
  0% {
    -webkit-transform: translateX(100px);
    transform: translateX(100px);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1; } }

.step_init_o {
  opacity: 0; }

.step_animateO {
  -webkit-animation: step_animateO 2s ease forwards;
  animation: step_animateO 2s ease forwards; }

@keyframes step_animateO {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes step_animateO {
  0% {
    opacity: 0; }
  100% {
